import React, { useState } from 'react';
import {
	Grid,
	Stack,
	Button,
	CircularProgress,
	makeStyles,
} from '@material-ui/core';
import { Formik } from 'formik';
import { gql, useMutation } from '@apollo/client';
import _get from 'lodash.get';
import { generatePath, useHistory } from 'react-router';
import { TSellRequest } from '../../../../../types';
import SettlementTermsTable from '../SettlementTerms/Table';
import { gridSpacing } from '../../../../../store/constant';
import AnimateButton from '../../../../../ui-component/extended/AnimateButton';
import graphqlSchema from '../../../../../graphql';
import { useSnackBar } from '../../../../../hooks';
import { getItemsKeys, prepareInitValue } from '../SettlementTerms/helper';
import { PATHS } from '../../../../../routes/path';
import { TABS } from '../../../../pages/property/EditPage';

type Props = {
	handleBack: () => void;
	formData: TSellRequest;
};

const useStyles = makeStyles({
	button: {
		marginRight: '10px',
	},
});

const AddSettlementTerms = ({ formData, handleBack }: Props) => {
	const classes = useStyles();
	const showSnackBar = useSnackBar();
	const history = useHistory();
	const { JurisdictionalCost: jurisdictionalCost = [] } = formData;
	const { PropertyId: propertyId } = formData;
	const [isApproving, setIsApproving] = useState<boolean>(false);
	const initialValues = prepareInitValue(jurisdictionalCost);
	const itemKeys = getItemsKeys(jurisdictionalCost);
	const [updatePropertyJurisdictionalCostPayors, { loading: isSaving }] =
		useMutation(gql(graphqlSchema.updatePropertyJurisdictionalCostPayors));
	const [approveAction] = useMutation(gql(graphqlSchema.approveProperty));

	const saveData = async (values: { [key: string]: string }) => {
		try {
			const payors = itemKeys.reduce<Array<{ [key: string]: string }>>(
				(lastPayors, itemKey) => {
					const payorId = _get(values, itemKey);
					if (!payorId) return [...lastPayors];
					return [
						...lastPayors,
						{
							PayorId: payorId,
							ItemKey: itemKey,
						},
					];
				},
				[]
			);
			await updatePropertyJurisdictionalCostPayors({
				variables: {
					Payors: payors,
					PropertyId: propertyId,
				},
			});
			showSnackBar({ message: 'Property updated', alertSeverity: 'success' });
		} catch (e) {
			const message = _get(e, 'message') || 'Something went wrong';
			showSnackBar({
				message,
			});
		}
	};

	const onSave = (values: { [key: string]: string }) => saveData(values);

	const onSubmit = async (values: { [key: string]: string }) => {
		try {
			setIsApproving(true);
			await saveData(values);
			await approveAction({
				variables: {
					PropertyId: propertyId,
				},
			});
			setIsApproving(false);
			const propertyLink = generatePath(PATHS.PROPERTY_EDIT, {
				id: propertyId,
				tab: TABS.actions,
			});
			history.push(propertyLink);
		} catch (e) {
			const message = _get(e, 'message') || 'Something went wrong';
			showSnackBar({
				message,
			});
			setIsApproving(false);
		}
	};
	const isDisabled = isApproving || isSaving;

	return (
		<Grid container spacing={gridSpacing}>
			<Grid item xs={12}>
				<Formik initialValues={initialValues} onSubmit={onSubmit}>
					{({ values, handleSubmit }) => (
						<form noValidate onSubmit={handleSubmit}>
							<Grid container spacing={3}>
								<Grid item xs={12} lg={12}>
									<SettlementTermsTable jurisdictionalCost={jurisdictionalCost} />
								</Grid>
								<Grid item xs={12}>
									<Stack direction='row' justifyContent='flex-end'>
										<AnimateButton>
											<Button onClick={handleBack} sx={{ my: 3, ml: 1 }}>
												Back
											</Button>
											<Button
												className={classes.button}
												color='primary'
												variant='outlined'
												type='button'
												disabled={isDisabled}
												sx={{ my: 3, ml: 1 }}
												onClick={() => onSave(values)}
											>
												{isSaving ? (
													<CircularProgress size='1.5rem' color='secondary' />
												) : (
													'Save'
												)}
											</Button>
											<Button
												color='primary'
												variant='contained'
												type='submit'
												disabled={isDisabled}
											>
												{isApproving ? (
													<CircularProgress size='1.5rem' color='secondary' />
												) : (
													'Approve'
												)}
											</Button>
										</AnimateButton>
									</Stack>
								</Grid>
							</Grid>
						</form>
					)}
				</Formik>
			</Grid>
		</Grid>
	);
};

export default AddSettlementTerms;
